.item{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 10px;
    width: 200px;

}
.item p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap:  20px;
   
}
.item-price-new{
    color: #374151;
    font-size: 18x;
    font-weight: 600;

}
.item-price-old{
    color: #8c8c8c;
    font-size:18px;
    font-weight: 500;
    text-decoration: line-through;

}
.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}